<template>
  <!--    -->
  <div class="post-detail" v-if="post.title">
    <h1>{{ post.title }}</h1>
    <p>{{ post.content }}</p>
    <div v-if="post.imagePath1">
      <template v-if="isImage(post.imagePath1)">
        <img
          class="post-image1"
          :src="`/uploads/${post.imagePath1}`"
          alt=" "
        />
        <button
          @click="downloadImage(post.imagePath1)"
          class="btn btn-secondary"
        >
           
        </button>
      </template>
      <template v-else>
        <p> : {{ getFileName(post.imagePath1) }}</p>
        <a
          :href="`/uploads/${post.imagePath1}`"
          target="_blank"
          class="btn btn-secondary"
          > </a
        >
      </template>
    </div>
    <div v-if="post.imagePath2">
      <template v-if="isImage(post.imagePath2)">
        <img
          class="post-image2"
          :src="`/uploads/${post.imagePath2}`"
          alt=" "
        />
        <button
          @click="downloadImage(post.imagePath2)"
          class="btn btn-secondary"
        >
           
        </button>
      </template>
      <template v-else>
        <p> : {{ getFileName(post.imagePath2) }}</p>
        <a
          :href="`/uploads/${post.imagePath2}`"
          target="_blank"
          class="btn btn-secondary"
          > </a
        >
      </template>
    </div>
    <div v-else>
      <p> </p>
    </div>
    <button @click="back"></button>
    <div v-if="isAuthor">
      <button @click="editPost" class="btn btn-primary"></button>
      <button @click="deletePost" class="btn btn-danger"></button>
    </div>
    <div>
      <textarea
        v-model="comment"
        placeholder=" ..."
        rows="4"
      ></textarea>
      <button @click="submitComment" class="btn btn-primary"> </button>
    </div>
    <div class="comments">
      <h2> </h2>
      <div v-for="comment in comments" :key="comment.id" class="comment-item">
        <p>{{ comment.content }}</p>
        <p>
          <strong>:</strong> {{ comment.author.username }} |
          <strong>:</strong>
          {{ formatDate(comment.createdAt) }}
        </p>
        <button
          v-if="comment.authorId === userStore.id"
          @click="deleteComment(comment.id)"
          class="btn btn-danger btn-sm"
        >
          
        </button>
      </div>
    </div>
  </div>
  <div v-else>
    <p>  ...</p>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router';
import { useUserStore } from '@/store/SignUp';
import { storeToRefs } from 'pinia';

const route = useRoute();
const router = useRouter();
const userStore = useUserStore();
const { isLoggedIn, id: userId } = storeToRefs(userStore);

const post = ref({
  title: '',
  content: '',
  id: 0,
  userId: 0,
  imagePath1: '', //   
  imagePath2:'',
});
const comments = ref([]);
const comment = ref('');
const isAuthor = computed(
  () => isLoggedIn.value && post.value.userId === userId.value
);

//    
const isImage = (filename) => {
  const extension = filename.split('.').pop().toLowerCase();
  return ['jpg', 'jpeg', 'png', 'gif','jfif'].includes(extension);
};

//    
const getFileName = (filename) => {
  return filename.split('/').pop();
};

const fetchPostDetails = async () => {
  try {
    await axios.put(`/posts/${route.params.id}/view`);

    const response = await axios.get(`/posts/${route.params.id}`);
    post.value = response.data;
    console.log('Post Data:', post.value); //   

    const commentsResponse = await axios.get(
      `/posts/${route.params.id}/comments`
    );
    console.log('Comments Data:', commentsResponse.data);
    comments.value = commentsResponse.data;
  } catch (error) {
    console.error('Error fetching post details:', error);
  }
};

const editPost = () => {
  router.push({
    name: 'ModPrRecruitmentNotice',
    params: { id: route.params.id },
  });
};

const back = () => {
  router.push({ name: 'RecruitmentNotice' });
};

const deletePost = async () => {
  if (!confirm('   ?')) return;

  try {
    await axios.delete(`/posts/${post.value.id}`);
    router.push({ name: 'RecruitmentNotice' }); //    
  } catch (error) {
    console.error('Error deleting post:', error);
    alert('    .');
  }
};

const submitComment = async () => {
  if (!comment.value.trim()) {
    alert('   .');
    return;
  }

  try {
    await axios.post(`/posts/${post.value.id}/comments`, {
      content: comment.value,
      authorId: userId.value, //   ID
    });
    comment.value = ''; //    
    fetchPostDetails(); //   
  } catch (error) {
    console.error('Error submitting comment:', error);
    alert('    .');
  }
};

const deleteComment = async (commentId) => {
  try {
    await axios.delete(`/posts/${post.value.id}/comments/${commentId}`);
    fetchPostDetails(); //   
  } catch (error) {
    console.error('Error deleting comment:', error);
    alert('    .');
  }
};

//   
const downloadImage = (filename) => {
  const url = `/posts/download/${filename}`;
  window.open(url, '_blank'); //   
};

const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
};

const logout = () => {
  userStore.isLoggedIn = false;
  userStore.isAdmin = false;
  router.push({ name: 'Home' });
};

//   
onMounted(() => {
  fetchPostDetails();
});
</script>

<style scoped></style>
